let GU = require('GameUtils');

let GameState = require("GameState");
let GameType = require("GameType");
let ItemType = require("ItemType");
let Global = require('Global');
let GameConfig = require("GameConfig");
const TankHeight =  GameConfig.TankHeight;
const TankWidth =  GameConfig.TankWidth;
const HalfTankWidth =  TankWidth*0.5;
const HalfTankHeight =  TankHeight*0.5;
const ItemWidth =  GameConfig.ItemWidth;

const BarTankDist = (TankWidth+8)*0.5;
const ItemTankDist = Math.pow((GameConfig.TankWidth+GameConfig.ItemWidth)*0.5,2);

cc.Class({
    extends:cc.Component,

    properties:{
        hard:'new', // new freshman  m  medium  h hard
        gameNodes:cc.Node, //block bar item
        bulletsNodes:cc.Node, //
        tanksNode:cc.Node,
        tankNode: {
          type:cc.Node,
            default:null,
            visible:false
        },
        stLabel:cc.Label,
        scoreLabel:cc.Label,
        camera: cc.Camera,
        gameState:GameState.init ,//0 init 1 playing 2 collision 3 gameover
        collisionNodeInfo:{
            type:cc.Object,
            default:null,
            visible:false
        }, //tankblockblock
        gameoverNode:cc.Node,
        particleNode:cc.Node,
        recordSpine:sp.Skeleton,
    },

    onLoad:function () {

        this.tankController = this.node.getComponent('TankController');
        this.blockController = this.node.getComponent('BlockMoveController');
        this.bulletController = this.node.getComponent('BulletController');
        this.gameoverNode.active = false;

        //UI
        this.updateScore();

        this.recordSpine.node.active = false;
        this.recordSpine.setEndListener(trackEntry => {
            var animationName = trackEntry.animation ? trackEntry.animation.name : "";
            cc.log("recordSpine [track %s][animation %s] end.", trackEntry.trackIndex, animationName);
            this.recordSpine.node.active = false;
        });

    },

    playNewRecord:function () {
        this.recordSpine.node.active = true;
        this.recordSpine.setAnimation(0, "newAnimation", false);
    },

    getTankSpeed:function () {
      return this.tankController.getTankSpeed();
    },

    //tankptblock
    getLineIndexByPtY:function (y) {
        let ret = Math.floor((y)/140);
        return ret;
    },

    //
    getCollumnIndexByPtX:function (ptx) {

        let x = ptx;
        if(ptx < 20){
            x = 20;
        }else if(ptx > 680){
            x  = 680;
        }
        let ret = Math.floor((x)/140);
        return ret;
    },


    //block
    // typetanknode
    //  {type:[null,GameType.Bar,GameType.Item],collumn:2,lineIndex:2}
    // type  block bar item
    getMoveBlockState:function (pt) {
        let lineIndex = this.getLineIndexByPtY(pt.y);
        let collumn  =  this.getCollumnIndexByPtX(pt.x+350);
        let ret = {type:GameType.Empty,collumn:collumn,lineIndex:lineIndex};
        console.log("getMoveBlockState:",pt.y,lineIndex,collumn,this.blockController.lineNodeInfo.length);
        if(lineIndex < 0){
            ret.lineIndex = 0;
            return ret;
        }

        let lineInfo = this.blockController.lineNodeInfo[lineIndex];
        let childs = lineInfo['child'];

        if(childs.length > 0){
            let childNodes = childs[collumn];
            if(childNodes){
                let type = GameType.Empty;
                for(let i = 0 ;i < childNodes.length; ++i){
                    if(childNodes[i]){
                        let com = childNodes[i].getComponent('GameComponent');
                        if(type < com.type){
                            type = com.type;
                        }
                    }
                }
                ret.type = type;
            }
        }
        return ret;
    },

    //row collumn
    getMoveBlockStateByRC:function (lineIndex,collumn) {
        let ret = {type:GameType.Empty,collumn:collumn,lineIndex:lineIndex};
        if(lineIndex < 0 ){
            return ret;
        }
        let lineInfo = this.blockController.lineNodeInfo[lineIndex];
        let childs = lineInfo['child'];
        if(childs.length > 0){
            let childNodes = childs[collumn];
            if(childNodes){
                let type = GameType.Empty;
                for(let i = 0 ;i < childNodes.length; ++i){
                    if(childNodes[i]){
                        let com = childNodes[i].getComponent('GameComponent');
                        if(type < com.type){
                            type = com.type;
                        }
                    }
                }
                ret.type = type;
            }
        }
        return ret;
    },

    // right true tank
    // Barbartankbartank
    // info {type:GameType.Bar,collumn:2,lineIndex:2}
    getDistRange:function (info,right = true) {

        let ret = [];
        let collumn = info.collumn;
        let infotype = info.type;
        if(infotype == GameType.Empty||
            infotype == GameType.Item){
            ret = [140*collumn,140*(collumn+1)];
        }else if(infotype == GameType.Block){
            if(right){
                ret = [140*collumn,140*collumn];
            }else{
                //left
                ret = [140*(collumn+1),140*(collumn+1)];
            }
        }else if(infotype == GameType.Bar){
            if(right){
                ret = [140*collumn,140*(collumn+1)-10];
            }else{
                ret = [140*(collumn+1),140*(collumn+1)];
            }
        }else{
            ret = [140*collumn,140*(collumn+1)];
        }
        return ret;
    },

    //tankbar [true,ptx]
    //barrightbar,barleft nodebar
    //bar
    autoChangeTankInBar:function (pt) {
        let lineIndex = this.getLineIndexByPtY(pt.y);
        if(lineIndex < 0){
           return [false,null];
        }
        let collumn  =  this.getCollumnIndexByPtX(pt.x+350);
        let currentInfo = this.getMoveBlockState(pt);
        if(currentInfo.type == GameType.Bar){
            let tmp = pt.x + 350;
            let x = 140*(collumn+1);
            let dist = Math.abs(x-tmp);
            if(dist < 24) {
                return [true,cc.p(x-350-24,pt.y)];
            }else if(dist >(140-20-4)){
                //left node
                return this.checkLeftBarForTank(pt,lineIndex,collumn-1);
                return [false,null];
            }else{
                return [false,null];
            }
        }else{
            return this.checkLeftBarForTank(pt,lineIndex,collumn-1);
        }
    },

    //tankbartank
    checkLeftBarForTank:function (pt,lineIndex,collumn) {
        if(collumn<0){
            return [false,null];
        }
        let currentInfo = this.getMoveBlockStateByRC(lineIndex,collumn);
        if(currentInfo.type == GameType.Bar){
            let tmp = pt.x + 350;
            let x = 140*(collumn+1);
            let dist = Math.abs(x-tmp);
            if(dist < 24) {
                return [true,cc.p(x-350-24,pt.y)];
            }else{
                return [false,null];
            }
        }else{
            return [false,null];
        }
    },


    //left middleright32node
    getRangeAtRC:function (lineIndex,collumn) {
        //node

        let range = [];

        let currentInfo = this.getMoveBlockStateByRC(lineIndex,collumn);
        let currentRange = this.getDistRange(currentInfo,currentInfo.type == GameType.Bar);
        if(currentInfo.type == GameType.Block){
            return currentRange;
        }

        if(collumn==0){
            let rightInfo = this.getMoveBlockStateByRC(lineIndex,collumn+1);
            let rightRange = this.getDistRange(rightInfo,true);
            range[0] = currentRange[0];
            if(rightInfo.type == GameType.Block)
            {
                range[1] = currentRange[1];
            }else if(rightInfo.type == GameType.Bar){
                if(currentInfo.type == GameType.Bar){
                    range[1] = currentRange[1];
                }else{
                    range[1] = rightRange[1];
                }
            }else if(currentInfo.type == GameType.Bar){
                range[1] = currentRange[1];
            }else{
                range[1] = rightRange[1];
            }

        }else if(collumn ==4){
            let leftInfo = this.getMoveBlockStateByRC(lineIndex,collumn-1);
            let leftRange = this.getDistRange(leftInfo,false);

            if( leftInfo.type == GameType.Bar||
                leftInfo.type == GameType.Block)
            {
                range[0] = currentRange[0];
            }else{
                range[0] = leftRange[0];
            }
            range[1] = currentRange[1];

        }else{

            let leftInfo = this.getMoveBlockStateByRC(lineIndex,collumn-1);
            let rightInfo = this.getMoveBlockStateByRC(lineIndex,collumn+1);

            let leftRange = this.getDistRange(leftInfo,false);
            let rightRange = this.getDistRange(rightInfo,true);

            if( leftInfo.type == GameType.Bar||
                leftInfo.type == GameType.Block)
            {
                range[0] = currentRange[0];
            }else{
                range[0] = leftRange[0];
            }

            if(rightInfo.type == GameType.Block)
            {
                range[1] = currentRange[1];
            }else if(rightInfo.type == GameType.Bar){
                if(currentInfo.type == GameType.Bar){
                    range[1] = currentRange[1];
                }else{
                    range[1] = rightRange[1];
                }
            }else if(currentInfo.type == GameType.Bar){
                range[1] = currentRange[1];
            }else{
                range[1] = rightRange[1];
            }
        }

        return range;
    },


    //tank
    //left middlerighttopbottomtop3bottom3
    //
    getDistRangeByPt2:function (pt) {

        let topIndex  =  this.getLineIndexByPtY(pt.y+20);
        let bottomIndex  =  this.getLineIndexByPtY(pt.y - 20);
        let collumn  =  this.getCollumnIndexByPtX(pt.x+350);
        let range  = [];
        if(topIndex == bottomIndex){
            let tmpRange =  this.getRangeAtRC(topIndex,collumn);
            range.push(...tmpRange);
        }else{
            //toprange
            let topRange =  this.getRangeAtRC(topIndex,collumn);
            //bottomrange
            let bottomRange =  this.getRangeAtRC(bottomIndex,collumn);

            //blockbottom
            if(topRange[0] == topRange[1]){
                range.push(...bottomRange);
            }else{
                let leftMax = topRange[0]>bottomRange[0]?topRange[0]:bottomRange[0];
                let rightMax = topRange[1]>bottomRange[1]?bottomRange[1]:topRange[1];
                range.push(leftMax);
                range.push(rightMax);
            }

        }
        return [range[0]+HalfTankWidth,range[1]-HalfTankWidth];
    },


    //tank 
    getDistRangeByPt:function (pt) {

        let collumn  =  this.getCollumnIndexByPtX(pt.x+350);

        let tankPreWidth = 20; //tankeupdate
        let width = 100;
        let topLeft = this.getMoveBlockState(cc.p(pt.x - width,pt.y+tankPreWidth));
        let topRight = this.getMoveBlockState(cc.p(pt.x + width,pt.y+tankPreWidth));
        let bottomLeft = this.getMoveBlockState(cc.p(pt.x - width,pt.y-tankPreWidth));
        let bottomRight = this.getMoveBlockState(cc.p(pt.x + width,pt.y-tankPreWidth));

        let topLeftRange = this.getDistRange(topLeft,false);
        let topRightRange = this.getDistRange(topRight,topRight.collumn > collumn);
        let bottomLeftRange = this.getDistRange(bottomLeft,false);
        let bottomRightRange = this.getDistRange(bottomRight,bottomRight.collumn > collumn);

        let leftMax =  topLeftRange[0]>bottomLeftRange[0]?topLeftRange[0]:bottomLeftRange[0];
        let rightmin = topRightRange[1]>bottomRightRange[1]?bottomRightRange[1]:topRightRange[1];

        //current 
        let currentRange =  [140*collumn,140*(collumn+1)];
        let currentInfo = this.getMoveBlockState(pt);
        if(currentInfo.type == GameType.Bar){
            currentRange =  [140*collumn,140*(collumn+1)-10];
        }

        let ret = [leftMax,rightmin];

        if(currentInfo.type == GameType.Bar){
            //tankbar
            if(ret[0] > currentRange[0]){
                ret[0] = currentRange[0];
            }
            ret[1] = currentRange[1];
        }else{
            if(ret[0] > currentRange[0]){
                ret[0] = currentRange[0];
            }

            if(ret[1] < currentRange[1]){
                ret[1] = currentRange[1];
            }
        }


        return [ret[0] + HalfTankWidth,ret[1]- HalfTankWidth];

    },

    //n
    checkChildNode:function (childs,collumn,pt) {
        //disttank
        let ret = {state:GameState.playing,mayState:GameState.playing,dist:0};
        let childNodes = childs[collumn];
        if(childNodes){
            for(let i =0 ; i < childNodes.length; ++i){
                if(childNodes[i]){
                    let com = childNodes[i].getComponent('GameComponent');
                    if(com.type == GameType.Block){
                        ret.state = GameState.collision;
                        ret.mayState = GameState.collision;
                        break;
                    }else if (com.type == GameType.Bar) {
                        //tankbar

                        let childptX = (collumn+1)*140;
                        let ptX = pt.x+350;
                        let dist = Math.abs(childptX-ptX);
                        if(dist <= BarTankDist){
                            ret.mayState = GameState.collision;
                            if(ptX>childptX){
                                //
                                ret.dist = BarTankDist-dist;
                            }else{
                                //
                                ret.dist = dist-BarTankDist;
                            }
                        }
                    }
                }
            }
        }
        return ret;
    },
    
    //. tankheadblock
    checkGameState:function (pt) {

        // state 
        // mayState bartank
        let ret = {state:GameState.init,mayState:GameState.playing,dist:0, collision:[]}; //collisionrow,collumn {lineIndex:0,collumn:0}
        if(this.tankController.head){
            // let pt = this.tankController.head.getPosition();
            let lineIndex = this.getLineIndexByPtY(pt.y + HalfTankHeight);
            let collumnLeft  =  this.getCollumnIndexByPtX(pt.x+350- 20); //20
            let collumnRight  =  this.getCollumnIndexByPtX(pt.x+350 + 20);
            let lineInfo = this.blockController.lineNodeInfo[lineIndex];
            let childs = lineInfo['child'];
            if(childs.length > 0){
                if(collumnLeft==collumnRight){
                    let childInfo = this.checkChildNode(childs,collumnLeft,pt);
                    ret.state = childInfo.state;
                    ret.mayState = childInfo.mayState;
                    ret.dist = childInfo.dist;
                    if(ret.state == GameState.collision)
                    {
                        ret.collision.push({lineIndex:lineIndex,collumn:collumnLeft});
                    }
                }else{
                    let ret1 = this.checkChildNode(childs,collumnLeft,pt);
                    let ret2 = this.checkChildNode(childs,collumnRight,pt);
                    ret.state =  ret1.state>ret2.state?ret1.state:ret2.state;
                    if(ret1.mayState>ret2.mayState){
                        ret.mayState = ret1.mayState;
                        ret.dist = ret1.dist;
                    }else{
                        ret.mayState = ret2.mayState;
                        ret.dist = ret2.dist;
                    }

                    if(ret1.state == GameState.collision)
                    {
                        ret.collision.push({lineIndex:lineIndex,collumn:collumnLeft});
                    }
                    if(ret2.state == GameState.collision)
                    {
                        ret.collision.push({lineIndex:lineIndex,collumn:collumnRight});
                    }
                }
            }
        }
        this.collisionNodeInfo = ret;
        return ret;
    },


    updateCamera:function (pt) {
        if(this.camera.node.active){
            this.camera.node.setPositionY(pt.y);
        }
    },

    //tankblock
    updateCollisionBlock:function () {
        if(this.collisionNodeInfo.state == GameState.collision){

            let len = this.collisionNodeInfo.collision.length;
            for(let i = 0; i < len;++i){
                let collision = this.collisionNodeInfo.collision[i];
                this.blockController.collisionBlock({collumn:collision['collumn'],lineIndex:collision['lineIndex']},1,false)
            }
        }
        // else{
        //
        //     console.error("call updateCollisionBlock but the state is not collision ",this.gameState);
        //     debugger;
        // }

    },

    //tanktankblock
    updateCollisionBlockI:function () {
        if(this.collisionNodeInfo.state == GameState.collision){
            let len = this.collisionNodeInfo.collision.length;
            for(let i = 0; i < len;++i){
                let collision = this.collisionNodeInfo.collision[i];
                this.blockController.collisionBlock({collumn:collision['collumn'],lineIndex:collision['lineIndex']},1,true)
            }
        }
        // else{
        //     console.error("call updateCollisionBlock but the state is not collision ",this.gameState);
        //     debugger;
        // }

    },

    //UI
    updateScore:function () {

        // console.log("updateScore : ",Global.showRecord , Global.score > Global.highScore);
        if(!Global.showRecord && Global.score > Global.highScore){
            this.playNewRecord();
            Global.showRecord = true;
        }
        this.scoreLabel.string  = Global.score.toString();
    },

    //tankitembuff
    checkItems:function () {
        let pt = this.tankController.head.getPosition();
        let lineIndex = this.getLineIndexByPtY(pt.y);
        if(lineIndex < 0){
            return;
        }
        let collumn  =  this.getCollumnIndexByPtX(pt.x+350);
        let lineInfo = this.blockController.lineNodeInfo[lineIndex];
        let childs = lineInfo['child'];
        if(childs.length > 0){
            let childNodes = childs[collumn];
            if(!childNodes){
                return;
            }
            let childNode = childNodes[2];
            if(childNode){
                let com = childNode.getComponent('GameComponent');
                let type = com.type;
                if(type == GameType.Item){
                    // 
                    let childNodePt = childNode.getPosition();
                    let y = lineIndex*140+70;
                    let dSQ = cc.pDistanceSQ(cc.p(childNodePt.x,y),pt);
                    console.log("dist:",childNodePt.x,y,pt,dSQ);
                    if(dSQ < ItemTankDist){
                        //fire Item
                        this.fireItems(com,com.subType,lineIndex,collumn);
                    }
                }
            }
        }
    },


    lateUpdate:function (dt) {

        // if(this.gameState == GameState.playing){
        //     this.checkItems();
        //     // this.collisionNodeInfo = this.checkGameState(this.tankController.head.getPosition());
        //     this.gameState = this.collisionNodeInfo.state;
        //
        // }
        // else if(this.gameState == GameState.collision){
        //     this.checkItems();
        //     this.collisionNodeInfo = this.checkGameState(this.tankController.head.getPosition());
        //     this.gameState = this.collisionNodeInfo.state;
        // }

        if(this.gameState == GameState.playing||this.gameState == GameState.collision){
            this.checkItems();
        }
    },
    
    updateState:function (st) {
        this.gameState = st;
        if(this.gameState == GameState.over||
            this.gameState == GameState.overWaiting){
            this.gameoverNode.active = true;
        }
    },
    
    //UI
    updateUI:function() {

        this.stLabel.string =  this.tankController.itemTypes.time.toFixed(3).toString();
        if(this.tankController.itemTypes.invincible){
            this.stLabel.string =  this.tankController.itemTypes.time.toFixed(3).toString();
        }else{
            if(this.tankController.itemTypes.one){
                this.stLabel.string = "one";
            }

            if(this.tankController.itemTypes.two){
                this.stLabel.string = "two";
            }

            if(this.tankController.itemTypes.double){
                this.stLabel.string = this.stLabel.string + " double";
            }
        }
    },

    // Iterm 
    fireItems:function (item,subType,lineIndex,collumn) {

        switch (subType){
            case ItemType.TankBody:
                //
                this.tankController.fireTankBody(item.count);
                break;
            case ItemType.One:
                this.tankController.fireOneBullet();
                break;
            case ItemType.Two:
                this.tankController.fireTwoBullet();
                break;
            case ItemType.Double:
                this.tankController.fireDobuleBullet();
                break;
            case ItemType.Invincible:
                this.tankController.fireInvincible(item.time);
                break;
            default:
                break;
        }

        this.blockController.deletItemNodeAt(item,lineIndex,collumn);
    },
    
    
    tankCanMove:function () {
        return this.tankController.tankCanMove();
    },

    //pt
    //,
    //bar
    // true false 
    tryBulletAtPt:function (pt) {

        let blockInfo  = this.getMoveBlockState(pt);
        // {type:GameType.Bar,collumn:2,lineIndex:2,node:node}
        if(blockInfo.type[0] == GameType.Bar){
            //bar  barx, (7+4)/2
            if(Math.abs((blockInfo.collumn+1)*140-350-pt.x) < (TankWidth+ItemWidth)*0.45){
                return true
            }
            return false;
        }
        else if(blockInfo.type == GameType.Block){
            this.blockController.collisionBlock(blockInfo,1);
            return true;
        }else{
            return false;
        }
    },
    
    gameOver:function () {
        this.tankController.head.active = false;
    },

    moreLife:function () {
        this.tankController.head.active = true;
    },


});